@use "../global/variables" as *;

.footer {
  padding: 30px 0;
  background: #2d2d44;
  color: #ffffff;
}

.footer__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
}

.footer__logo {

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.footer__text {
  text-align: center;
}
